import {  Post as IPost} from "./main";


interface Props{
    post: IPost
}


export const Post = (props:Props) =>{
    const {post} = props
    return (
        <div  className="Front" >
               <div     className="Homes"  >
        <div className="title" >
            <h1>{post.title}</h1>

        </div>
        <div className="bodyy" >
            <p>{post.description}</p>

        </div>
        <div className="footer" >
            <p>@{post.username}</p>

        </div>


    </div>
        </div>
 
    )
}